.profile-container {
    height: 80vh;
    width: 100%;
}

.profile-header {
    text-align: center;
    font-size: 36px;
    color: var(--text-color);
}

.profile-subheader {
    text-align: center;
    font-size: 30px;
    color: var(--text-color);
}

.profile-content {
    padding-left: 10vh;
}

.profile-input-field {
    width: 200px;
    height: 30px;
    margin: 5px;
    padding: 3px 7px;
    font-size: 17px;
    border-color: var(--text-color);
    border-radius: 3px;
    transition: 0.3s all;
}

.profile-btn-logout {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    white-space: nowrap;
    background-color: transparent;
    color: var(--text-color);
    border: 1px solid var(--text-color);
    transition: all 0.3s ease-out;
    margin: 2.5px 10px;
}

.profile-btn-logout:hover {
    color: var(--background-color);
    background-color: var(--text-color);
    transition: all 0.2s ease-out;
}